import { useNavigate } from "react-router-dom";
import Membership from "./Membership";

const Header = () => {
    const navigate = useNavigate();
    return (
            <header>
                <nav>
                   <h1>Netflix </h1>
                   <div>
                    <select>
                        <option></option>
                        <option>English</option>
                    </select>
                    <button onClick={()=>{
                        navigate("/login");
                    }}></button>
                   </div>
                </nav>
                <div className="header-content">
                    <h2>, <br/></h2>
                    <h4>5,500 .    .</h4>
                    <Membership />
                </div>
            </header>
    );
};

export default Header;