import { useState } from "react";
import { useNavigate } from "react-router-dom";

const Login = () => {
    const [emailFocused, setEmailFocused] = useState(false);
    const [PwFocused, setPwFocused] = useState(false);
    const [emailError, setEmailError] = useState(false);
    const [PwError, setPwError] = useState(false);
    const [isCode, setIsCode] = useState(false);
    const navigate = useNavigate();

    const handleSubmit = (event)=>{
        event.preventDefault();
    }
    return (
        <div className="login">
            <div className="login-popup">
             <p className="close" onClick={()=>{navigate("/")}}></p>
            <h2></h2>
            <form onSubmit={handleSubmit}>
                <label className={`${emailFocused ? "focused":""} ${emailError ? "error":""}`}>
                    <span>    </span>
                    <input 
                    type="text"
                    onFocus={()=>{
                        setEmailFocused(true);
                        setEmailError(false);
                        }}
                    onBlur={(e)=>{
                        if(e.target.value === ""){
                            setEmailFocused(false);
                            setEmailError(true);
                        }
                    }}
                    />
                </label>
                {emailError && <p className="email-error">     .</p>}
                {
                    !isCode && (<label
                    className={`${PwFocused ? "focused":""} $ ${PwError ? "error":""}`}
                >
                <span></span>
                <input 
                type="password"
                onFocus={()=>{
                    setPwFocused(true);
                    setPwError(false);
                }}
                onBlur={(e)=>{
                    if(e.target.value === ""){
                    setPwFocused(false);
                    setPwError(true);
                    }}
                }
                />
                </label>)}
                {PwError && <p className="email-error">  4~60  .</p>}
                {isCode && <p>      .</p>}
                <button type="submit">
                {isCode ? "  " : ""}
                </button>
                <p></p>
                <button onClick={()=>{
                    setIsCode(!isCode);
                    setEmailError(false);
                    setEmailFocused(false);
                    setPwError(false);
                    }}>
                    {isCode ? " " : "  "}
                </button>
                <a href="#">
                    {isCode ? "   ?" : " ?"}
                    </a>
            </form>
            <div className="login-box">
                <label>
                    <input type="checkbox"/>
                    <span>  </span>
                    </label>
                    <p>Netflix  ?<a href="#"> </a></p>
                    <p>  Google reCAPTCHA      .</p>
                    <a href="#"> .</a>
            </div>
        </div>
    </div>
    );
};

export default Login;